.right {
  z-index: 1;
  display: flex;
  flex-direction: row-reverse;
}
ion-content {
  --offset-bottom: auto;
  --overflow: auto;
  overflow: auto;
}
body {
  overflow: hidden;
}

.modalContainer {
  padding-top: 50px;
}
.chat-box {
  z-index: 10;
  position: fixed;
  bottom: 0;
  width: 100%;
}
.chat-button {
  position: relative;
  top: 5px;
}
.chat-container {
  /* background-image: url(https://www.transparenttextures.com/patterns/green-gobbler.png); */
  position: relative;
  width: 100%;
}

